@import url(https://fonts.googleapis.com/css?family=Noto+Serif|Playfair+Display|Roboto+Slab|Slabo+27px);
@import url(https://fonts.googleapis.com/css?family=Cinzel);
@import url(https://fonts.googleapis.com/css?family=Cardo);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

H1 {
  font-size: 2em; }

.body {
  height: calc(100vh - 50px);
  width: 100vw;
  background-image: url(/ninaandroo/static/media/mountains.35c68035.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  display: block; }

.about_us_container {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 21px;
  margin: 14px 0; }

.about_us_title {
  font-family: "Cardo", serif;
  font-size: 40px;
  text-align: left;
  margin-bottom: 10px; }

.img_about_container {
  display: flex;
  flex-direction: column; }

.photo {
  width: 173px;
  border-radius: 6px; }

.about_us_body {
  font-family: "Cardo", serif;
  font-size: 14px;
  text-align: justify;
  line-height: 17px; }

.about_us_subtitles {
  margin: 11px 0 12px;
  font-size: 23px;
  font-weight: 500;
  text-align: left; }

@media (max-width: 600px) {
  .photo {
    display: block;
    margin: 0 auto; }
  .about_us_title {
    text-align: center; } }

.coming {
  font-family: "Cardo", serif;
  font-size: 40px;
  text-align: center;
  margin: 15px; }

.email_yt_insta_container {
  display: flex; }

.contact_title {
  font-family: "Cardo", serif;
  font-size: 40px;
  text-align: left;
  margin: 15px 0 10px 36px; }

.contact_blurb {
  font-family: "Cardo", serif;
  font-size: 15px;
  line-height: 21px;
  text-align: left;
  margin: 0 10px 10px 36px; }

.name_input {
  font-family: "Noto Serif", serif;
  font-size: 20px;
  width: 400px;
  height: 30px;
  margin: 5px; }

.email_input {
  font-family: "Noto Serif", serif;
  font-size: 20px;
  width: 400px;
  height: 30px;
  margin: 5px; }

.message_input {
  font-family: "Noto Serif", serif;
  font-size: 20px;
  width: 400px;
  height: 400px;
  overflow: scroll;
  white-space: pre-line;
  margin: 5px; }

.input_container {
  display: flex;
  flex-direction: column;
  align-items: center; }

.submit {
  border-radius: 3px;
  width: 140px;
  height: 31px;
  margin-top: 9px;
  font-size: 20px;
  font-family: "Noto Serif", serif;
  background-color: #3a3838;
  color: white; }

.submit:hover {
  background-color: #272626; }

#cu_box {
  width: 300px;
  margin: 29px auto; }

@media (max-width: 800px) {
  .contact_title {
    text-align: center;
    margin: 20px; }
  .contact_blurb {
    text-align: center;
    margin: 15px; } }

.insta_fb_container {
  display: flex;
  align-items: center;
  justify-content: center; }

#home_links {
  width: 147px;
  height: 103px;
  padding: 9px 4px; }

.yt_container {
  margin: 5px;
  opacity: 0.5;
  transition: 1s;
  border-style: solid;
  border-color: black;
  border-width: 2px;
  padding: 4px; }

.yt_container:hover {
  opacity: 1;
  transition: 1s; }

.insta_container {
  margin: 5px;
  opacity: 0.5;
  transition: 1s;
  border-style: solid;
  border-color: black;
  border-width: 2px;
  padding: 4px; }

.insta_container:hover {
  opacity: 1;
  transition: 1s; }

.insta {
  height: 64px;
  margin-left: 5px; }

.youtube {
  height: 63px; }

.text {
  color: black;
  height: 19px;
  margin: 4px;
  font-size: 16px;
  font-family: "Cinzel", serif;
  background-color: whitesmoke;
  padding: 6px;
  border-style: solid;
  border-color: black;
  border-width: 2px; }

.thanks {
  font-family: "Cardo", serif;
  font-size: 40px;
  text-align: center;
  margin: 27px 13px;
  border-style: solid;
  border-width: 1.5px 0;
  padding: 13px; }

.thanks_blurb {
  font-family: "Cardo", serif;
  font-size: 24px;
  line-height: 34px;
  text-align: center;
  margin: 10px; }

.follow {
  font-style: italic;
  background-color: rgba(255, 255, 255, 0.7);
  margin: 0 0 10px; }

.routes_container {
  margin-left: 7%;
  width: 75%;
  height: 50px;
  display: grid;
  grid-template-columns: 20% 20% 30% 20% 20%;
  text-decoration: none; }

.route_container {
  height: 26px;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); }

.route_container:hover {
  border-style: solid;
  border-color: black;
  border-width: 1px 0; }

.route_container_active {
  border-style: solid;
  border-color: black;
  border-width: 1px 0; }

.route_links {
  color: black;
  font-size: 11px;
  padding: 0;
  font-family: "Cardo", serif;
  font-weight: 100;
  display: inline-block;
  line-height: 26px; }

link {
  text-decoration: none; }

.ninaandroo {
  color: black;
  text-decoration: none;
  font-size: 13px;
  font-weight: 600;
  margin-top: 5px;
  border-style: solid;
  padding: 7px 0 0px 0;
  border-width: 1px;
  width: 99%;
  height: 30px;
  font-family: "Cinzel", serif;
  background-color: white;
  letter-spacing: 2px; }

@media (min-width: 500px) {
  .ninaandroo {
    letter-spacing: 0px;
    font-size: 15px; } }

@media (min-width: 430px) {
  .ninaandroo {
    letter-spacing: 0px;
    font-size: 15px;
    line-height: 24px; } }

@media (min-width: 625px) {
  .ninaandroo {
    letter-spacing: 0px;
    font-size: 18px; }
  .routes_container {
    margin-left: 15%;
    width: 64%; } }

@media (min-width: 900px) {
  .ninaandroo {
    letter-spacing: 4px;
    font-size: 21px; } }

.video_container {
  height: 195px;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 30px;
  display: flex;
  margin-bottom: 10px; }

.video_title {
  font-family: "Cardo", serif;
  font-size: 28px;
  text-align: left;
  margin: 0 10px 10px 10px; }

.video_description {
  font-family: "Cardo", serif;
  font-size: 15px;
  line-height: 21px;
  text-align: justify;
  margin: 0 10px 10px 10px;
  height: 174px;
  overflow: scroll; }

@media (max-width: 830px) {
  .video_title {
    font-size: 20px; } }

@media (max-width: 730px) {
  .video_container {
    display: block;
    height: 355px;
    padding: 21px; }
  .body {
    overflow: scroll; }
  .video_title {
    margin: 8px 10px 4px 10px; } }

